.header {
  position: fixed;
  width: 100%;
  top: 0;
  border-bottom: 1px solid var(--line-color);
  box-shadow: 0px 0px 3px var(--accent-color);
  overflow: hidden;
  z-index: 100;

  @media (max-width: 678px) {
    overflow: visible;
  }

  &__container {
    padding-block: rem(20);
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;

    @media (max-width: 1280px) {
      padding-block: rem(10);
    }

    &::before {
      content: '';
      position: absolute;
      inset: 0;
      opacity: 0.8;
      backdrop-filter: blur(100px);
      z-index: 10;
      border-radius: 12px;
    }
  }

  @keyframes progress-animation {
    0% {
      transform: scaleX(0);
    }
    100% {
      transform: scaleX(1);
    }
  }

  .progress-bar {
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    width: 100%;
    height: 5px;

    transform-origin: center;
    background-color: var(--accent-color);
    opacity: 0.7;

    animation: progress-animation linear;
    animation-timeline: scroll();
  }
}


